/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"
import GitHubButton from "react-github-btn"
import { Link } from "gatsby"

import { rhythm } from "../utils/typography"
import "../styles/bio.css"

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
        childImageSharp {
          fixed(width: 140, height: 140) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      wave: file(absolutePath: { regex: "/waving-hand.png/" }) {
        childImageSharp {
          fixed(width: 32, height: 32) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
            summary
          }
          social {
            twitter
          }
        }
      }
    }
  `)
  // const { author, social } = data.site.siteMetadata
  // return (
  //   <div
  //     style={{
  //       display: `flex`,
  //       marginBottom: rhythm(2.5),
  //     }}
  //   >
  //     <p>
  //       Written by <strong>{author.name}</strong> {author.summary}
  //       {` `}
  //       <a href={`https://twitter.com/${social.twitter}`}>
  //         You should follow him on Twitter
  //       </a>
  //     </p>
  //   </div>
  // )

  return (
    <div
      style={{
        display: `flex`,
        marginBottom: rhythm(2.5),
        alignContent: `center`,
      }}
    >
      <div class="flex md:flex-row-reverse flex-wrap">
        <div class="w-full md:w-2/5 text-gray-700">
          <div class="badge max-w-sm rounded overflow-hidden shadow-2xl">
            <div class="px-8 pt-8">
              <div class="font-bold text-white text-center text-xl mb-2">
                <Image
                  fixed={data.avatar.childImageSharp.fixed}
                  style={{
                    marginRight: rhythm(1 / 2),
                    marginBottom: 0,
                    minWidth: 50,
                    borderRadius: `100%`,
                  }}
                  imgStyle={{
                    borderRadius: `50%`,
                  }}
                />
              </div>
              <p class="font-light text-indigo-200 text-center text-sm font-light">
                <b>Msc in Data Science</b>
                <br /> Unversity of essex
                <br /> Colchester, UK
              </p>
            </div>
            <div class="px-6 py-4">
              <span class="inline-block bg-gray-200 text-indigo-600	rounded-full px-3 py-1 text-xs font-semibold text-gray-700 mb-2 mr-2">
                #photography
              </span>
              <span class="inline-block bg-gray-200 text-indigo-600 rounded-full px-3 py-1 text-xs font-semibold text-gray-700 mr-2">
                #travel
              </span>
              <span class="inline-block bg-gray-200 text-indigo-600 rounded-full px-3 py-1 text-xs font-semibold text-gray-700">
                #winter
              </span>
            </div>
          </div>
        </div>
        <div class="w-full md:w-3/5 text-gray-700">
          <div>
            <h1 className="font-bold text-4xl py-8">
              Hey, I'm Viji
              <Image fixed={data.wave.childImageSharp.fixed} />
            </h1>
          </div>
          <p className="font-light text-gray-600 text-sm">
            I'm a data scientist, experienced in various machine learning
            methods and
            <Link
              className="text-indigo-500 font-semibold hover:bg-indigo-600 hover:text-white hover:rounded"
              to="/articles"
            >
              {" "}
              writing{" "}
            </Link>
            about data analyst.
          </p>
          <div className="pt-6">
            <GitHubButton
              href="https://github.com/varadharajanviji"
              data-color-scheme="no-preference: dark; light: light; dark: dark;"
              data-size="large"
              data-show-count="true"
              aria-label="Follow @varadharajanviji on GitHub"
            >
              Follow @varadharajanviji
            </GitHubButton>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Bio
